.modal-scrollbar-measure {
  position: absolute;
  top: -9999px;
  width: 50px;
  height: 50px;
  overflow: scroll; }

.modal-backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 998;
  background: rgba(white, 0.27);
  &.fade {
    transition: opacity 0.15s linear;
    opacity: 0; }
  &.in {
    opacity: 1; } }

.modal {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  padding: 20px;
  z-index: 999;
  white-space: nowrap;
  @include vertical; }

.modal-dialog {
  display: inline-block;
  vertical-align: middle;
  width: 100%;
  max-width: 640px;
  font-size: 18px;
  text-align: left;
  white-space: normal; }

.modal-content {
  margin-bottom: auto;
  background: white;
  border: 1px solid #979797;
  box-sizing: border-box; }
