  left: 6px;
}

.radio > input:checked ~ span:after,
.check > input:checked ~ span:after {
  opacity: 1;
}

.radio > span,
.check > span {
  display: block;
  padding-left: 8px;
}

.radio > span:before,
.check > span:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 24px;
  height: 24px;
  border-radius: 50%;
  border: 1px solid;
  box-sizing: border-box;
}

.radio > span:after,
.check > span:after {
  content: "";
  position: absolute;
  top: 6px;
  left: 6px;
  border-radius: 50%;
  width: 12px;
  height: 12px;
  background: #4A90E2;
  opacity: 0;
  transition: all 0.2s ease;
}

.radio_simple > span,
.check_simple > span {
  padding-left: 0;
}

.check > span:before, .check > span:after {
  border-radius: 0;
}

